import React from 'react';
import KaKaoLogin from 'react-kakao-login';
import {useCookies} from 'react-cookie';




const Kakao =  (props) =>{

    const responseKakao = (response) => {
        console.log("카카오로부터 받아온 정보 값:" + response);
        console.dir(response);

        setCookies("token", response.response.access_token);
        setCookies("name", response.profile.properties.nickname);

        props.closeModal();
    }

    const [Cookies, setCookies] = useCookies(['name']);

    return( 
        <div>
            <KaKaoLogin 
            jsKey="80f50894c622d1470ceea24da0bea846"
            buttonText="KaKao"
            onSuccess={responseKakao}
            getProfile={true}
            useDefaultStyle    
            />
        </div> 
    );

}

export default Kakao;