import React, {useState} from 'react';
import axios from 'axios';
import {useCookies} from 'react-cookie';
import SingleComment from './SingleComment';


function Comments(props){
    
    console.log("여기는 싱글 커멘트이다. 아래는 가져온 코멘트 리스트들이다.");
    console.dir(props.commentList);


    const [CommentsValue, setCommentsValue] = useState("");
    const commentsHandler = (event) =>{
        setCommentsValue(event.currentTarget.value);
    }

    const[Cookies] = useCookies();

 
    const submitHandler = (event)=>{
        event.preventDefault();

        if(Cookies.token == "undefined" || !Cookies.token){
            alert("로그인 후 이용해주세요.");
            return;
        }

        const value = {
            content: CommentsValue,
            writer: Cookies.name,
            postId: props.postId
        }

        axios.post('http://localhost:3003/comments/saveComments', value).then(
            response => {
                if(response.data.success){
                    alert("댓글등록 완료");
                    props.updateComment(response.data.comments);
                    setCommentsValue("");
                }else{
                    alert("댓글 등록 실패");
                }
            }
        );
    }


    return (
        <>

         {props.commentList && props.commentList.map((comment, index) => (
             <SingleComment comment={comment}/>
        ))}

        <div>
           <p> 댓글 </p>
        </div>
        <form style={{display:'flex'}} onSubmit={submitHandler}>
            <textarea
                style={{width:'100%', borderRadius: '5px'}}
                onChange={commentsHandler}
                value={CommentsValue}
                placeholder="댓글을 작성하세요"
            />
        <br />
        <button style={{width:'20%', height: '52px'}} onClick={submitHandler}>등록</button>
        </form>
        </>
    )

}

export default Comments;

