import React, { useState } from 'react';
import {
    Button,
    Alert,
    UncontrolledAlert,
    Card,
    CardBody,
    CardTitle,
    Col,
    Row,
    CardSubtitle,
    CardText
} from 'reactstrap';
import { useEffect } from 'react';
import axios from 'axios';
import ImageSlider from './ImageSlider';
import Page from './Page';
import {useCookies} from 'react-cookie';
import SearchPost from './SearchPost';



const Dayook = () => {

      const [Post, setPost] = useState([]);
      const [CurrentPage, setCurrentPage] = useState(0);
      
      
      
      
      const [DataCount, setDataCount] = useState(0);
   /*  let DataCount = 0; */

      //const [SearchWord, setSearchWord] = useState("");

      const [Cookies] = useCookies();

    /*   console.log("초기 페이지 값: " + CurrentPage); */


      console.log("현 시점에서 Post값" + Post);
      console.log("현 시점에서 CurrentPage 값:" + CurrentPage);
      console.log("현 시점에서 DataCount  값" + DataCount);
    
      useEffect(() => {
        
        console.log("유즈이펙트 발동함");

        axios.get(`http://localhost:3003/post/posts?category=1&CurrentPage=${CurrentPage}`).then(response => {
            if(response.data.success){
                setPost(response.data.docs);
                setDataCount(response.data.realcount);
                console.log("서버에서 보내온 응답 값:" +response.data.realcount);
            }else{
                alert("상품 가져오는데 실패함");
            }
        });
    }, [CurrentPage]);   



    function searchPostFunc(param){
        console.log("searchPostFunc 메소드 탔다");
        console.log("검색데이터:" + param);
        

        setCurrentPage(0);
        axios.get(`http://localhost:3003/post/posts?category=1&CurrentPage=${CurrentPage}&title=${param}`).then(response => {
            if(response.data.success){
                console.log("서버에서 보내온 응답값2는 아래에 찍혀있다.");
                console.dir(response.data);

                setPost(response.data.docs);
                setDataCount(response.data.realcount);
                
               
            }else{
                alert("검색 상품 가져오기 실패");
            }
        });
    }


    const updateCurrentPage = (param) => {
        setCurrentPage(param);
        console.log("페이지 변경해서 넘어온 파라미터값:" + param);
        console.log("페이지 변경한 후의 CurrentPage 값"+ CurrentPage);
    } 

    const updateSearch = (param) =>{
        console.log("서치한 값:" + param);
      //  setSearchWord(param);
        searchPostFunc(param);
    }


    const renderCards = Post.map((post, index)=> {

        return <Col lg={3} md={4} xs={6}>
            <Card>
                    <a href={`#/postDetail/${post._id}`}><ImageSlider images={post.images}/></a>
                    <CardBody>
                        <CardTitle>{post.title}</CardTitle>
                        <CardSubtitle>Card subtitle</CardSubtitle>
                        <CardText>{post.description}</CardText>
                    </CardBody>
            </Card>
        </Col>
    });




return <div>
            <div style={{textAlign: 'center'}}>
                <h1>다육이</h1>
            </div>
            <br/>
            <br/>
         {Cookies.token && Cookies.name == "채인욱" && <div style={{textAlign: 'right'}}>
                 <a href={`#/ui-components/tooltip`}><Button>글 쓰기</Button></a>
            </div> 
        }
            <br/>
            <br/>
            <div style={{textAlign: 'right'}}>
                <SearchPost searchFunction={updateSearch}/>
            </div>
            <br/><br/>
            <div> 
                <Row>
                    {renderCards}
                </Row>
            </div>
            <br />
            <br />
           <div style={{display:'flex', alignItems: 'center', justifyContent: 'center'}}>
                <Page updateCurrentPage={updateCurrentPage} CurrentPage={CurrentPage} DataCount={DataCount}/> 
            </div>

       </div> 
}

export default Dayook;
