import React, {useState} from 'react';
import {
    Row,
    Col,
    Card,
    CardBody,
    Input,
    Form,
    FormGroup,
    Label,
    FormText,
    Button
} from 'reactstrap';

import Axios from 'axios';

import { SalesSummary, Projects, Feeds, SocialCards } from 'components/dashboard-components';

import FileUpload from '../utils/FileUpload';
//import Carousel from '../ui-components/carousel.jsx';
import Carousel from '../ui-components/Carousel.js';
//import im from './img5.jpg';
import CK from './ckeditor';
import { propTypes } from 'react-bootstrap/esm/Image';

const WriteBoard = (props) => {

    const Categorys = [
        {key:"1", value:"다육이"},
        {key:"2", value:"야생화"},
        {key:"3", value:"화기"},
        {key:"9", value:"농장지킴이"}
    ];


    const[Images, setImages] = useState([]);
    const[Title, setTitle] = useState("");
    const[Description, setDescription] = useState("");
    const[Category, setCategory] = useState("1");


    const updateImages = (newImages) => {
        setImages(newImages);
    }


    const submitHandler = (event) => {
        event.preventDefault();
        const body = {
            images: Images,
            title: Title,
            description: Description,
            category: Category
        }
        
        Axios.post("http://localhost:3003/post/", body)
        .then(response => {
            if(response.data.success){
                alert("상품 업로드 성공");
                props.history.push('/');
            }else{
                alert("상품 업로드 실패");
            }
        });
    }
    
    const titleChangeHandler = (event) =>{
        setTitle(event.currentTarget.value);
    }




    const categoryHandler = (event) => {
        setCategory(event.currentTarget.value);
    }

    const descriptionHandler = (event)=>{
        setDescription(event.currentTarget.value);
    }

    return (

    <div style={{maxWidth: '900px', margin: '2rem auto'}}>
       <div style={{textAlign: 'center'}}>
            <h2>글 쓰기</h2>
       </div>
       
       <br />
       <div style={{float:"right"}}>
            <select onChange={categoryHandler} value={Category}>
                {Categorys.map(item => (
                    <option key={item.key} value={item.key}>{item.value}</option>
                ))}
            </select>
       </div>
       <br />
       <br />
        <Form onSubmit={submitHandler}>
            <FileUpload refreshFunction={updateImages} />
            <br />
            <br />
            <label>상품 명</label>
            <Input  onChange={titleChangeHandler} value={Title}/>
            <br />
            <br />
                <Label for="exampleText">설명</Label>
                <Input type="textarea" onChange={descriptionHandler} />
            <br />
            <br />
            <Button type="submit">확인</Button>

                    {/* <div>

                        <CK/>
                    </div> */}
        </Form>
    </div>
    
       
    );
}

export default WriteBoard;