import React from 'react';
import FacebookLogin from 'react-facebook-login';
import {useCookies} from 'react-cookie';


const FB = (props) => {

    const responseFacebook = (response) => {
   //  console.log("토큰찍어본다:" + response.accessToken);

      console.log("페이스북으로 부터 받아온 정보 값 :" + response);
      console.dir(response);

      setCookies("token", response.accessToken, { path: '/' });
      setCookies("name", response.name);

      props.closeModal();
  }

    const [Cookies, setCookies] = useCookies(['name']);

    return (
        <div>
            <FacebookLogin
                appId="372029687175525"
                autoLoad={false}
                fields="name"
                callback={responseFacebook}
                size="small"
            />
        </div>
    );
}

export default FB; 
