import React, {useState} from 'react';
import Dropzone from 'react-dropzone';
import axios from 'axios';
//import {Icon} from '@ant-design/icons';


function FileUpload(props){


    const [Images, setImages] = useState([]);


    const dropHandler = (files) => {
        let formdata = new FormData();

        const config = {
            header: {'content-type' : 'multipart/form-data'}
        }

        formdata.append("file", files[0]);

        axios.post('http://localhost:3003/post/image', formdata, config)
        .then(response => {
            if(response.data.success){

                console.log("클라이언트 성공");
                console.log(response.data);


                setImages([...Images, response.data.filePath]);
                props.refreshFunction([...Images, response.data.filePath]);
            }else{
                console.log("클라이언트 실패");
                alert('저장 실패');
            }
        });
    }


    const deleteHandler = (image) => {
        const currentIndex = Images.indexOf(image);
        let newImages = [...Images];

        newImages.splice(currentIndex, 1);
        setImages(newImages);
        props.refreshFunction(newImages);    
    }

    return(
        <div style={{display: 'flex', justifyContent: 'space-between'}}>
            <Dropzone onDrop={dropHandler}>
                {({getRootProps, getInputProps}) => (  
                            <div 
                                style={{width:300, height:240, border: '1px solid lightgray',
                                display: 'flex', alignItems: 'center', justifyContent: 'center'}} 
                                {...getRootProps()}>
                                <input {...getInputProps()} />

                                <svg width="3em" height="3em" viewBox="0 0 16 16" class="bi bi-upload" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                    <path fill-rule="evenodd" d="M.5 9.9a.5.5 0 0 1 .5.5v2.5a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-2.5a.5.5 0 0 1 1 0v2.5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2v-2.5a.5.5 0 0 1 .5-.5z"/>
                                    <path fill-rule="evenodd" d="M7.646 1.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1-.708.708L8.5 2.707V11.5a.5.5 0 0 1-1 0V2.707L5.354 4.854a.5.5 0 1 1-.708-.708l3-3z"/>
                                </svg>
                            {/*    <Icon type="plus" style={{fontSize: '3rem'}} />  */}
                            </div>
                    )}
            </Dropzone>

            <div style={{display: 'flex', width:'570px', height:'240px', overflowX: 'scroll'}}>      
               {Images.map((image, index) => (
                   <div onClick={() => deleteHandler(image)} key={index}>
                       <img style={{minwidth: '300px', width: '300px', height: '240px'}}
                           src={`http://localhost:3003/${image}`}
                       />
                   </div>      
               ))}           
           </div> 
        </div>
    );


}

export default FileUpload;