import React, { useEffect, useState } from 'react';
import axios from 'axios'
import { Row, Col } from 'reactstrap'
import PostImage from '../views/ui-components/PostImage';
import PostInfo from '../views/ui-components/PostInfo';
import Comments from '../views/ui-components/Comments';


function PostDetail(props){
   
    const [Post, setPost] = useState({});
    const [Comment, setComment] = useState([]);

    const updateComment = (param) =>{
        console.log("★★★★★★★★★★★★★★");
        setComment(Comment.concat(param));
    }

    const post_id = props.match.params.postId;

    useEffect(() => {
        
        axios.get(`http://localhost:3003/post/postDetailById?id=${props.match.params.postId}`).then(response => {
            if(response.data.success){
                console.log("데이터 성공적으로 응답 받음");
                setPost(response.data.post[0]);
            }else{
                console.log("데이터 응답 실패");
            }
        });

        axios.post('http://localhost:3003/comments/getComments', {post_id})
        .then(response => {
            if(response.data.success){
                setComment(response.data.comments);
            }else{
                alert("댓글 가져오기 실패");
            }
        });
    }, []);
    

    return( 
        <div style={{width:'100%', padding: "3rem 4rem"}}>

            <div style={{display: 'flex', justifyContent: "center"}}>
                <h1>{Post.title} </h1>
            </div>
            <br />
            <br />
             <Row>
                <Col lg={6} sm={12}>
                    <PostImage detail={Post}/>
                </Col>
                <Col lg={6} sm={12}>
                   <PostInfo detail={Post}/>
                </Col> 
            </Row> 
            <br />
            <br />
            <Comments postId={props.match.params.postId} commentList={Comment} updateComment={updateComment}/>

    </div>
    )
}

export default PostDetail;


