import React, { Component } from 'react';
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import axios from 'axios';

import {Input, Button} from 'reactstrap';



class Ck extends Component {

    

    constructor(props){
        super(props);
        
        this.state = {            
             contents : ''
        };

        this.handleSubmit = this.handleSubmit.bind(this);     
    }
  

    handleInputChange = (data) =>{
        
    
       this.setState({contents: data});
       console.log(this.state.contents);
    
    }

    handleSubmit(){
        alert('등록 완료');

        var title = document.getElementById("title").value;
       //var b = document.getElementById("title").value;

        console.log(this.state.contents);

      /* axios.post(
            'http://localhost:3003/process/addpost',
             {contents: this.state.contents, title: title },            
            {headers: {"Content-Type": `application/json`}}
        );  */

        axios.post(
            'http://localhost:3003/process/listpost'        
        ); 
    }

    
   
    render() {

    
       
        return (
            <>

                        <form onSubmit={this.handleSubmit}>
                                <div className="App">
                                    <h2>글 쓰기</h2>

                                <div class="text-right"> 
                                    <Button>등록</Button>
                                </div>
                                    <br/>
                                    <Input type="text" id="title" placeholder="제목 입력" />               
                                    <br/>
                                
                                
                                    <CKEditor
                                        editor={ ClassicEditor }
                                        data=""
                                        onInit={ editor => {
                                            // You can store the "editor" and use when it is needed.
                                            console.log( 'Editor is ready to use!', editor );
                                        } }


                                        onChange={ ( event, editor ) => {
                                            
                                            const data = editor.getData();
                                            this.handleInputChange(data);

                                            console.log( { event, editor, data } );
                                        } }


                                        onBlur={ editor => {
                                            console.log( 'Blur.', editor );
                                        } }


                                        onFocus={ editor => {
                                            console.log( 'Focus.', editor );
                                        } }

                                        config = {
                                            {
                                                ckfinder : {uploadUrl: 'http://localhost:3003/upload'}
                                            }
                                        }

                        
                                       
                                    />
 

                                </div>
                        </form>
            
            </>
        );
    }
}

export default Ck;